import React from 'react';
import SuitableCrewItem from "./SuitableCrewItem";
import classes from "./SuitableCrewList.module.css";


const SuitableCrewList = (props: any) => {
  return(
    <div className={classes.list}>
      {props.data.map((prop: any) => (
        <SuitableCrewItem
          key={prop.id}
          car_mark={prop.car_mark}
          car_model={prop.car_model}
          car_color={prop.car_color}
          car_number={prop.car_number}
        />
      ))}
    </div>
  )
}

export default SuitableCrewList;