import React from 'react';
import classes from './OrderForm.module.css';


const OrderForm = () => {
  return (
    <div>
      <div className={classes.input}>
        <input
          placeholder="Откуда"
          name="from"
          id="from"
        />
      </div>
      <div className={classes.input}>
        <input
          placeholder="Куда"
          name="where"
          id="where"
        />
      </div>
    </div>
  )
}

export default OrderForm;